.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.display-none {
  display: none;
}
@keyframes articles-menu-opening {
  from {
    width: 0;
  }
  to {
    left: 0;
    width: 310px;
  }
}
@keyframes articles-menu-closing {
  to {
    left: -320px;
    width: 0;
  }
}
.articles-menu-opening {
  left: -320px;
  /* stylelint-disable-next-line declaration-no-important */
  width: 0;
  position: relative;
  animation-name: articles-menu-opening;
  animation-duration: 1.6s;
  animation-fill-mode: forwards;
}
.articles-menu-closing {
  left: 0;
  position: relative;
  animation-name: articles-menu-closing;
  animation-duration: 1.6s;
  animation-fill-mode: forwards;
}
@keyframes articles-button-open {
  from {
    background: url("../img/open-menu-button.svg") no-repeat center;
  }
  to {
    background: url("../../../img/articlesButtonMenuActive.svg") no-repeat center;
  }
}
@keyframes articles-button-close {
  to {
    background: url("../img/open-menu-button.svg") no-repeat center;
  }
}
.articles-menu-button-open {
  animation-name: articles-button-open;
  animation-duration: 1.6s;
  animation-fill-mode: forwards;
}
.articles-menu-button-close {
  animation-name: articles-button-close;
  animation-duration: 1.6s;
  animation-fill-mode: forwards;
}
html {
  height: 100%;
}
.main-page {
  font-family: "Inter", sans-serif;
  min-height: 100%;
  min-width: 100%;
  display: flex;
  margin: 0;
  background: url("../img/background.png") no-repeat;
  background-size: cover;
  padding: 18px;
  box-sizing: border-box;
}
.main-container {
  max-width: 1940px;
  width: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
.main-header {
  display: flex;
  max-width: 97vw;
}
.nav {
  flex-grow: 1;
  box-sizing: border-box;
}
.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
.nav__item {
  background-color: #ffffff;
  border-radius: 15px;
}
.nav__item--top-menu {
  width: 88%;
  min-width: fit-content;
}
.nav__item--articles-menu {
  width: 7%;
  min-width: fit-content;
  padding: 0 12px;
}
.nav__link {
  display: block;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  text-decoration: none;
  padding: 21px 0;
  color: #000000;
}
.nav__button {
  padding: 0;
  background: url("../img/articlesButtonMenuActive.svg") no-repeat center;
  border: none;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.articles-menu {
  background-color: #ffffff;
  padding: 26px 14px;
  border-radius: 15px;
  margin-top: 24px;
  width: 310px;
  box-sizing: border-box;
}
.articles-menu__title {
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  margin: 0;
  margin-bottom: 26px;
}
.articles-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.articles-menu__item {
  position: relative;
  cursor: pointer;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
}
.articles-menu__item:not(:last-child) {
  margin-bottom: 25px;
}
.articles-menu__item--active {
  left: 15px;
  background-color: #73efb3;
}
.articles-menu__button {
  padding: 0;
  margin-left: 8px;
  border: none;
  background: none;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 35px;
  line-height: 42px;
  text-align: center;
  cursor: pointer;
}
.articles-menu__button--vs-code {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) -130.26%, #00bfe9 68.42%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.articles-menu__button--git {
  color: #916800;
}
.articles-menu__button--styles {
  color: #039be5;
}
.articles-menu__button--html {
  color: #000000;
}
.articles-menu__button--javascript {
  color: #000000;
}
.articles-menu__button--gulp {
  color: #d24946;
}
.articles-menu__icon {
  display: block;
  width: 48px;
  height: 48px;
  margin-left: 3.5px;
}
.content-container {
  max-width: 97vw;
  display: flex;
}
.main-content {
  padding: 20px 48px;
  width: 86vw;
  margin: 0 auto;
}
.my-info {
  width: fit-content;
  font-family: "Inter", sans-serif;
  font-style: normal;
  color: #ffffff;
  display: flex;
  margin: 0 auto;
  margin-bottom: 50px;
}
.my-info__title {
  text-align: center;
  font-size: 45px;
  line-height: 54px;
  margin: 0;
  margin-top: 45px;
  margin-bottom: 26px;
  padding-right: 15px;
}
.my-info__list {
  list-style: none;
  padding: 0;
  margin: 0;
  max-width: 582px;
}
.my-info__item {
  background: rgba(255, 255, 255, 0.44);
  border-radius: 50px;
  display: flex;
  align-items: center;
}
.my-info__item:not(:last-child) {
  margin-bottom: 10px;
}
.my-info__item-icon {
  padding-left: 10px;
  margin-right: 15px;
}
.my-info__content-title {
  margin: 0;
  font-weight: 500;
  font-size: 23px;
  line-height: 28px;
}
.my-project__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.my-projects__item {
  margin-bottom: 30px;
  height: 181px;
  box-shadow: 0 4px 10px 5px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
}
.my-project__img {
  border-radius: 15px;
}
.articles-page {
  font-family: "Inter", sans-serif;
  font-style: normal;
  color: #ffffff;
}
.articles-page__buttons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.articles-page__title {
  font-weight: 600;
  font-size: 40px;
  line-height: 48px;
  margin-top: 30px;
  margin-bottom: 54px;
  text-align: center;
  text-transform: uppercase;
}
.articles-page__button-container {
  width: 295px;
  margin-bottom: 20px;
}
.articles-page__button-title {
  font-weight: 500;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  margin: 0;
}
.articles-page__button {
  border: none;
  background: no-repeat center;
  width: 100%;
  height: 223px;
  cursor: pointer;
  border-radius: 15px;
}
